body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.heading_style {
  padding: 30px 0;
  text-align: center;
  font-family: "cursive";
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 500;
  font-size: 1.5rem;
  margin-bottom: 30px;
  background-color: rgb(234, 234, 234);
  color: black;
}
.cards {
  width: 100%;
  height: auto;
}
.heading {
  color: aqua;
}
.card {
  margin: 6%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: white;
  width: 21.25;
  border-radius: 13px;
  box-shadow: 36px 10px 55px -2px rgba(0, 0, 0, 0.1);
  float: left;
 width: 19rem;
}
.card_img {
  width: 100%;
  height: 239px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  padding: 16px 24px 24px 24px;
  background-color: white;
}
.card_info {
  background-color: white;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  padding: 16px 24px 24px 24px;
}
.card_category {
  text-transform: uppercase;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: gray;
  background-color: white;
}
.card_title {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: "cursive";
  background-color: white;
  text-transform: capitalize;
}
button {
  padding: 5px 8px;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 500;
  outline: none;
  border: 3px solid black;
  background-color: white;
  color: #868686;
  cursor: pointer;
}
.card:hover{
  box-shadow: 0px 30ox 8px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05,1.05);
}